* {
  transition: 0.4s;
  transition-duration: 500ms;
}
div.about-main {
  width: 100%;
  margin: 5rem 0;
}
div.about-main h2 {
  text-align: center;
  font-size: 50px;
  margin: 30px 0;
  color: #ff7b54;
}
div.about-main div.flex div.about-responsive img {
  display: none;
}
div.about-main div.flex {
  display: flex;
}
div.about-main div.flex div.right {
  margin-right: 10rem;
  margin-left: 1rem;
}

div.about-main div.flex div.right img {
  height: 380px;
  width: auto;
  border-radius: 20px;
}
div.about-main div.flex div.left {
  margin-left: 10rem;
  margin-right: 1rem;
  height: 400px;
  padding-top: 4rem;
}
div.about-main div.flex div.left a {
  color: black;
  border: 1px solid #ff7b54;
  background: #ff7b54;
  padding: 10px 16px;
  border-radius: 30px;
  transition: 0.3s;
  transition-duration: 500ms;
  font-size: 14px;
  font-weight: 100;
  letter-spacing: 1.5px;
}
div.about-main div.flex div.left div.but {
  text-align: end;
  margin-top: 5rem;
  width: 100%;
}

div.about-main div.flex div.left div.but a {
  border-radius: 0;
  box-shadow: 7px 7px #fff;
}
div.about-main div.flex div.left div.but a:hover {
  box-shadow: none;
  border-radius: 5px;
}

div.about-main div.flex div.left a:hover {
  color: #ff7b54;
  background: rgb(21, 19, 19);
  border: 1px solid #ff7b54;
}
/*** RESPONSIVE FOR LAPTOPS ***/
/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
  div.about-main div.flex div.left div.but {
    text-align: end;
  }
  div.about-main div.flex div.right {
    margin-right: 10rem;
  }
  div.about-main div.flex div.right img {
    height: 310px;
  }
  div.about-main div.flex div.left a {
    padding: 7px 11px;
    font-size: 12px;
  }
  div.about-main div.flex div.left p {
    font-size: 14px;
  }
  div.about-main div.flex div.left div.but {
    margin-bottom: 1rem;
  }
}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.about-main div.flex div.left a {
    padding: 8px 13px;
    border-radius: 30px;
    transition: 0.3s;
    transition-duration: 500ms;
    font-size: 12px;
    font-weight: 100;
    letter-spacing: 1.5px;
  }
  div.about-main div.flex div.left a {
    font-size: 10px;
    padding: 7px 12px;
  }
  div.about-main div.flex div.left p {
    font-size: 14px;
  }
  div.about-main div.flex div.right img {
    height: 250px;
  }
  div.about-main div.flex div.right {
    margin-right: 7rem;
    margin-left: 0;
  }
  div.about-main div.flex div.left {
    margin-left: 7rem;
    padding-top: 2rem;
  }
  div.about-main div.flex div.left div.but {
    margin-top: 0rem;
  }
}

@media screen and (max-width: 576px) {
  div.about-main h2 {
    color: #ff7b54;
  }
  div.about-main div.flex {
    display: block;
  }
  div.about-main div.flex div.left {
    margin: 0 4rem;
    height: auto;
    padding: 0;
  }
  div.about-main div.flex div.right img {
    display: none;
  }

  div.about-main div.flex div.about-responsive {
    width: 100%;
    display: grid;
    justify-items: center;
  }

  div.about-main div.flex div.about-responsive img {
    display: block;
    height: 300px;
    text-align: center;
    width: auto;
    border-radius: 20px;
  }

  div.about-main div.flex div.left div.but a {
    border-radius: 0;
    box-shadow: 5px 5px #fff;
  }
  div.about-main div.flex div.left a {
    color: black;
    border: 1px solid #ff7b54;
    background: #ff7b54;
  }
  div.about-main div.flex div.left a:hover {
    color: #ff7b54;
    border: 1px solid #ff7b54;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.about-main h2 {
    color: #ff7b54;
  }
  div.about-main {
    width: 100%;
    margin: -5rem 0 0 0;
  }
  div.about-main div.flex div.about-responsive img {
    height: 250px;
    text-align: center;
    width: auto;
    border-radius: 20px;
  }
  div.about-main div.flex div.left p {
    font-size: 12px;
  }
  div.about-main div.flex div.left div.but {
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
  }
  iv.about-main div.flex div.left a {
    color: black;
    border: 1px solid #ff7b54;
    background: #ff7b54;
  }
  div.about-main div.flex div.left a:hover {
    color: #ff7b54;
    border: 1px solid #ff7b54;
  }
  div.about-main div.flex div.left div.but a {
    border-radius: 0;
    box-shadow: 3px 3px #fff;
  }
}
