h5,
h3,
h1 {
  font-size: 60px;
  color: #fff;
}
h1 span {
  color: #ff7b54;
  cursor: pointer;
}
p {
  font-size: 20px;
  color: rgb(48, 47, 47);
  margin: 20px 0;
}
div.main_content {
  margin: 0;
  margin-left: -4rem;
}

div.contact_me button {
  color: black;
  border: 1px solid #ff7b54;
  background: #ff7b54;
  padding: 14px 25px;
  transition: 0.3s;
  transition-duration: 500ms;
  font-size: 16px;
  font-weight: 100;
  letter-spacing: 1.5px;
  box-shadow: 8px 8px #fff;
  margin: 20px 0;
}
div.contact_me button:hover {
  color: #ff7b54;
  background: rgb(21, 19, 19);
  border: 1px solid #ff7b54;
  box-shadow: none;
  border-radius: 5px;
}

/***HIDDEN CONTENT **/
div.main_content div.contact-hidden {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: var(--z-modal);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transition-duration: 500ms;
}
div.main_content div.contact-hidden div.contact-hidden-block {
  width: 700px;
  position: relative;
  background: rgb(255, 255, 255);
  padding: 2rem 4rem;
  border-radius: 20px;
}
div.main_content div.contact-hidden div.contact-hidden-block p {
  font-size: 35px;
  margin: 0;
  color: #000;
}
div.main_content div.contact-hidden div.contact-hidden-block i.uil-times {
  position: absolute;
  top: 1rem;
  right: 1.5rem;
  font-size: 35px;
  color: #000;
  cursor: pointer;
}
div.main_content div.contact-hidden div.contact-hidden-block div.hidden-icons {
  margin: 1rem 0;
  display: flex;
  width: 100%;
  justify-content: space-around;
}
div.main_content
  div.contact-hidden
  div.contact-hidden-block
  div.hidden-icons
  a
  i {
  font-size: 55px;
  color: #000;
}
div.main_content div.hidden-active {
  opacity: 1;
  visibility: visible;
}
/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
  h5,
  h3,
  h1 {
    font-size: 40px;
  }
  div.main_content {
    margin-left: -2rem;
  }
  p {
    font-size: 15px;
  }
  div.contact_me button {
    padding: 10px 20px;
    transition: 0.3s;
    transition-duration: 500ms;
    font-size: 13px;
    font-weight: 100;
    letter-spacing: 1.5px;
  }
}

/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.main_content {
    margin-left: -3rem;
  }
  div.main_content div.contact-hidden div.contact-hidden-block {
    width: 400px;
  }
  div.main_content div.contact-hidden div.contact-hidden-block p {
    font-size: 25px;
  }
  div.main_content div.contact-hidden div.contact-hidden-block i.uil-times {
    font-size: 25px;
    top: 2rem;
    right: 2rem;
  }
  div.main_content
    div.contact-hidden
    div.contact-hidden-block
    div.hidden-icons
    a
    i {
    font-size: 35px;
  }
}

@media screen and (max-width: 576px) {
  h1 span {
    color: #ff7b54;
  }
  h5,
  h3,
  h1 {
    font-size: 30px;
  }
  div.main_content {
    margin-left: 0rem;
    width: 100%;
    text-align: center;
  }
  div.main_content div.contact-hidden div.contact-hidden-block {
    width: 350px;
  }
  div.main_content div.contact-hidden div.contact-hidden-block p {
    font-size: 20px;
  }
  div.main_content div.contact-hidden div.contact-hidden-block i.uil-times {
    font-size: 20px;
  }
  div.contact_me button {
    color: black;
    border: 1px solid #ff7b54;
    background: #ff7b54;
    box-shadow: 8px 8px #fff;
  }
  div.contact_me button:hover {
    color: #ff7b54;
    background: rgb(21, 19, 19);
    border: 1px solid #ff7b54;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.contact_me button {
    color: black;
    border: 1px solid #ff7b54;
    background: #ff7b54;
    box-shadow: 4px 4px #fff;
  }
  h1 span {
    color: #ff7b54;
  }
  h5,
  h3,
  h1 {
    font-size: 25px;
  }
  p {
    font-size: 11px;
  }
  div.contact_me button {
    padding: 5px 15px;
    transition: 0.3s;
    transition-duration: 500ms;
    font-size: 10px;
    font-weight: 100;
    letter-spacing: 1.5px;
    margin: 5px 0;
  }
  div.main_content div.contact-hidden div.contact-hidden-block {
    width: 250px;
  }
  div.main_content div.contact-hidden div.contact-hidden-block p {
    font-size: 15px;
  }
  div.main_content div.contact-hidden div.contact-hidden-block i.uil-times {
    font-size: 20px;
  }
  div.main_content
    div.contact-hidden
    div.contact-hidden-block
    div.hidden-icons
    a {
    text-align: center;
    margin: 0 0.75rem;
  }
  div.main_content
    div.contact-hidden
    div.contact-hidden-block
    div.hidden-icons
    a
    i {
    font-size: 25px;
  }
  div.main_content
    div.contact-hidden
    div.contact-hidden-block
    div.hidden-icons {
    margin: 2rem 0 0 0;
  }
}
