.header {
  width: 100%;
  height: 5rem;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
}
div.header nav.head {
  display: flex;
  height: 5rem;
  justify-content: space-around;
  align-items: center;
  width: auto;
}
div.header nav.head div.nav-li ul {
  display: flex;
}
div.header nav.head div.nav-li ul li {
  list-style: none;
  margin: 0 1.25rem;
  transition: 0.3s;
}
div.header nav.head .name {
  font-size: var(--h1-font-size);
  color: #ffffff;
  transition: 0.3s;
  cursor: pointer;
}
div.header nav.head .name span.s {
  font-size: 60px;
  color: #ff7b54;
}
div.header nav.head .name span.a {
  font-size: 40px;
  color: #ff7b54;
}
div.header nav.head .name span.g {
  font-size: 28px;
  color: #ff7b54;
}
div.header nav.head .name span.aa {
  font-size: 40px;
  color: #ff7b54;
}
div.header nav.head .name span.r {
  font-size: 60px;
  color: #ff7b54;
}
div.header nav.head div.nav-li ul li a {
  font-size: var(--font-semi-bold);
  color: #ffffff;
  transition: 0.3s;
  transition-duration: 500ms;
}
div.header nav.head div.nav-li ul li a:hover {
  color: #ff7b54;
}
div.header nav.head div.nav-li ul div.dropdown {
  display: flex;
}
div.header i {
  font-size: var(--h2-font-size);
  cursor: pointer;
  display: none;
}

/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.header {
    top: initial;
    bottom: 0;
    position: fixed;
    border-radius: 25px 25px 0 0;
  }
  div.header .name {
    display: none;
  }

  div.header nav.head div.nav-li ul li {
    text-align: center;
    margin: 0 15px;
  }
  div.header nav.head div.nav-li ul li a {
    font-size: 15px;
  }
  div.header nav.head div.nav-li ul li i.nav__i {
    display: block;
    font-size: var(--h1-font-size);
  }
  div.header div.dropdown {
    align-items: center;
  }
  div.header i.uil-times {
    display: none;
  }
}

@media screen and (max-width: 576px) {
  div.header {
    position: fixed;
  }
  div.header nav.head div.nav-li ul li {
    text-align: center;
    margin: 0 10px;
    display: block;
  }
  div.header nav.head div.nav-li ul li a {
    font-size: 9px;
  }
  div.header nav.head div.nav-li ul li i.nav__i {
    font-size: 20px;
  }
  div.header nav.head .name span.s {
    font-size: 60px;
    color: #ff7b54;
  }
  div.header nav.head .name span.a {
    font-size: 40px;
    color: #ff7b54;
  }
  div.header nav.head .name span.g {
    font-size: 28px;
    color: #ff7b54;
  }
  div.header nav.head .name span.aa {
    font-size: 40px;
    color: #ff7b54;
  }
  div.header nav.head .name span.r {
    font-size: 60px;
    color: #ff7b54;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.header {
    height: 60px;
    position: fixed;
  }
  div.header nav.head {
    height: 60px;
    align-items: center;
  }
  div.header nav.head div.nav-li ul li {
    text-align: center;
    margin: 0 3px;
  }
  div.header nav.head div.nav-li ul li a {
    font-size: 8x;
  }
  div.header nav.head div.name {
    display: block;
  }
  div.header nav.head .name span.s {
    font-size: 30px;
    color: #ff7b54;
  }
  div.header nav.head .name span.a {
    font-size: 20px;
    color: #ff7b54;
  }
  div.header nav.head .name span.g {
    font-size: 12px;
    color: #ff7b54;
  }
  div.header nav.head .name span.aa {
    font-size: 20px;
    color: #ff7b54;
  }
  div.header nav.head .name span.r {
    font-size: 30px;
    color: #ff7b54;
  }
  div.header nav.head div.nav-li ul li a {
    display: none;
  }
  div.header i.uil-apps {
    display: block;
  }
  div.header nav.head div.nav-li ul li i.nav__i {
    display: none;
  }
  div.header nav.head div.showMenu ul li i.nav__i {
    display: block;
    font-size: 15px;
    transition: 0.3s;
    transition-delay: 500ms;
  }
  div.header nav.head div.showMenu ul li a {
    display: block;
    font-size: 8px;
    transition: 0.3s;
    transition-delay: 500ms;
  }
  div.header nav.head div.showMenu ul div.dropdown i.uil-apps {
    display: none;
  }
  div.header nav.head div.showMenu ul div.dropdown i.uil-times {
    display: block;
    transition: 0.3s;
    margin: 0 5px;
    font-size: 30px;
    transition-duration: 500ms;
  }
  div.header nav.head div.name-dissap {
    display: none;
  }
}
