div.main1 {
  display: grid;
  position: fixed;
}
div.main1 div {
  width: 120px;
  text-align: center;
  border-radius: 30px;
  position: relative;
  right: 50px;
  cursor: pointer;
  margin: 2px 0;
  background: #ff7b54;
  transition: 0.4s;
  transition-duration: 200ms;
}
div.main1 div:hover {
  position: relative;
  right: 35px;
}

div.main1 div a i {
  color: #fff;
  margin: 0 0 0 40px;
  font-size: 33px;
  transition: 0.3s;
  transition-duration: 500ms;
}

@media screen and (max-width: 972px) {
  div.main1 div {
    width: 100px;
    text-align: center;
    border-radius: 30px;
    position: relative;
    right: 50px;
    cursor: pointer;
    margin: 2px 0;
  }
  div.main1 div:hover {
    position: relative;
    right: 40px;
  }
}
@media screen and (max-width: 576px) {
  div.main1 div {
    width: 100px;
    text-align: center;
    border-radius: 30px;
    position: relative;
    right: 50px;
    cursor: pointer;
    margin: 2px 0;
  }
  div.main1 div a i {
    color: #fff;
    margin: 0px 0 0 40px;
    font-size: 28px;
  }
}
