/*****google fonts*/
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital@1&family=Montserrat:wght@300&family=Poppins:wght@500;600&display=swap");

/***** ====== CSS VARIABLES ====== ******/

:root {
  --header-height: 3rem;

  /*Colors*/

  --hue: 0;
  --sat: 0%;
  --title-color: hsl(var(--hue), var(--sat), 20%);
  --title-color-dark: hsl(var(--hue), var(--sat), 0%);
  --text-color: hsl(var(--hue), var(--sat), 45%);
  --body-color: hsl(var(--hue), var(--sat), 98%);
  --container-color: #fff;

  /*** FONT AND TYPOGRAPHY ***/

  --body-font: "Poppins", sans-serif;

  /**** .5rem = 8px \ 1rem = 16px ****/

  --big-font-size: 3.5rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /**** FONT WEIGHT ***********/

  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;

  /** MARGINS BOTTOM ****/

  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /******* Z - INDEX **********/

  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/**** RESPONSIVE TYPOGRAPHY *****/
@media screen and (max-width: 992px) {
  :root {
    --big-font-size: 2.75rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/*** BASE *********/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: 0.3s;
  transition-duration: 500ms;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}
body {
  background: rgb(10, 10, 10);
  color: var(--text-color);
}
h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

/****** REUSUABLE CSS CLASSES ******/

.section {
  padding: 6rem 0 2rem;
}
.section-title {
  font-size: var(--h1-font-size);
  color: var(--title-color);
}

.section-subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;
}

.section-subtitle,
.section-title {
  text-align: center;
}

/**** CONTAINER *********/

.container {
  margin-left: auto;
  margin-right: auto;
}
.grid {
  display: grid;
  gap: 1.5rem;
}

/****** BUTTONS *****/
.button {
  display: inline-block;
  background-color: var(--title-color);
  color: var(--container-color);
  padding: 1.25rem 2rem;
  font-weight: var(--font-medium);
}
.button:hover {
  background-color: var(--title-color-dark);
}

.button-icon {
  margin-left: var(--mb-0-5);
}

.button-flex {
  display: inline-flex;
  align-items: center;
}
.main {
  align-items: center;
}
