.scroll-up-main {
  position: fixed;
  right: 2.5rem;
  bottom: -20%;
  cursor: pointer;
  background: rgb(59, 58, 58);
  border-radius: 10px;
}
.scroll-up-main i {
  padding: 0.25rem;
  font-size: 25px;
  color: #ff7b54;
}
.scroll-up-main:hover {
  background: #ff7b54;
}
.scroll-up-main:hover i {
  color: black;
}

.show-scroll {
  bottom: 3rem;
}
/*** RESPONSIVE FOR LAPTOPS ***/

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
  .scroll-up-main {
    right: 1.5rem;
    pad: 0.25rem 0.4rem;
  }
  .scroll-up-main i {
    font-size: 1.25rem;
  }
}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  .show-scroll {
    bottom: 5.5rem;
  }
}

@media screen and (max-width: 576px) {
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  .show-scroll {
    bottom: 4rem;
  }
  .scroll-up-main {
    right: 0.75rem;
  }
}
