footer.foot-main {
  width: 100%;
  background: rgb(36, 35, 35);
  height: 100%;

  border-top: 2px solid rgb(44, 44, 44);
  border-radius: 30px 30px 0 0;
}

.foot-block h3 {
  font-size: 25px;
  text-align: center;
}
.social ul {
  display: flex;
  justify-content: center;
  column-gap: 2rem;
  margin: 1.5rem 0;
}
.social ul li a {
  font-size: 30px;
  color: #fff;
}

.social ul li a:hover {
  color: rgb(138, 132, 132);
}

.copy-right {
  text-align: center;
  font-size: 10px;
}
/*** RESPONSIVE FOR LAPTOPS ***/

/***FOR LARGE DEVICE ****/

/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  footer.foot-main {
    padding: 0 0 5.5rem 0;
  }
}

@media screen and (max-width: 576px) {
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  footer.foot-main {
    padding: 0 0 4rem 0;
  }
  .copy-right {
    font-size: 8px;
  }
}
