div.services-main {
  width: 100%;
}

div.services-main div.services-name {
  width: 100%;
  text-align: center;
}
div.services-main div.services-name p {
  font-size: 45px;
  margin: 0;
  color: #ff7b54;
}
div.services-main div.services-name h2 {
  font-size: 13px;
  color: #fff;
}

div.services-main div.disp-flex {
  display: flex;
  justify-content: center;
}
div.services-main div.disp-flex div.services-block {
  width: 250px;
  height: 200px;
  margin: 2rem 5rem;
  display: grid;
  text-align: center;
  align-items: center;
  background: #ff7b54;
  box-shadow: 10px 10px white;
  transition: 0.4s;
}
div.services-main div.disp-flex div.services-block div.services-content i {
  color: #fff;
}
div.services-main div.disp-flex div.services-block:hover {
  box-shadow: none;
  border-radius: 10px;
}
div.services-main div.disp-flex div.services-block div.services-content {
  padding-bottom: 3rem;
}
div.services-main div.disp-flex div.services-block i.uil-arrow {
  font-size: 65px;
}
div.services-main div.disp-flex div.services-block i.uil-edit {
  font-size: 65px;
}
div.services-main div.disp-flex div.services-block p {
  color: #fff;
  margin: 0;
}
div.services-main
  div.disp-flex
  div.services-block
  div.services-content
  div.services-btn {
  display: flex;
  justify-content: center;
  font-size: 15px;
  margin: 1rem 0;
  color: #fff;
  cursor: pointer;
  transition: 0.4s;
}

/**** HIDDEN BLOCKS **/

div.services-main div.services-hidden-ui-ux,
div.services-main div.services-hidden-vis-des {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: var(--z-modal);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transition-duration: 500ms;
}

div.services-main div.services-hidden-ui-ux div.services-hidden-block,
div.services-main div.services-hidden-vis-des div.services-hidden-block {
  width: 700px;
  position: relative;
  background: rgb(255, 255, 255);
  padding: 2rem 4rem;
  border-radius: 20px;
}
div.services-main
  div.services-hidden-ui-ux
  div.services-hidden-block
  i.uil-times,
div.services-main
  div.services-hidden-vis-des
  div.services-hidden-block
  i.uil-times {
  position: absolute;
  top: 2rem;
  right: 2rem;
  font-size: 25px;
  color: #000;
  cursor: pointer;
}
div.services-main div.services-hidden-ui-ux div.services-hidden-block p.ui-name,
div.services-main
  div.services-hidden-vis-des
  div.services-hidden-block
  p.ui-name {
  text-align: center;
  font-size: 35px;
}
div.services-main div.services-hidden-ui-ux div.services-hidden-block h2,
div.services-main div.services-hidden-vis-des div.services-hidden-block h2 {
  font-size: 15px;
  text-align: center;
}

/** HIDDEN - ACTIVE **/
div.services-main div.hidden-active {
  opacity: 1;
  visibility: visible;
}

/*** RESPONSIVE FOR LAPTOPS ***/

/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.services-main div.disp-flex {
    margin: 3rem 0;
  }
  div.services-main div.disp-flex div.services-block {
    margin: 2rem 3rem;
  }
  div.services-main div.services-hidden-ui-ux div.services-hidden-block,
  div.services-main div.services-hidden-vis-des div.services-hidden-block {
    width: 700px;
    position: relative;
    background: rgb(255, 255, 255);
    padding: 1rem 1rem;
    margin: 3rem;
  }
  div.services-main
    div.services-hidden-ui-ux
    div.services-hidden-block
    p.ui-name,
  div.services-main
    div.services-hidden-vis-des
    div.services-hidden-block
    p.ui-name {
    font-size: 25px;
  }
  div.services-main div.services-hidden-ui-ux div.services-hidden-block h2,
  div.services-main div.services-hidden-vis-des div.services-hidden-block h2 {
    font-size: 10px;
    text-align: center;
  }
  div.services-main
    div.services-hidden-ui-ux
    div.services-hidden-block
    div.hidden-content-ui-ux
    i,
  div.services-main
    div.services-hidden-vis-des
    div.services-hidden-block
    div.hidden-content-ui-ux
    i {
    font-size: 12px;
  }
  div.services-main
    div.services-hidden-ui-ux
    div.services-hidden-block
    i.uil-times,
  div.services-main
    div.services-hidden-vis-des
    div.services-hidden-block
    i.uil-times {
    position: absolute;
    top: 1rem;
    right: 1rem;
  }
}
@media screen and (max-width: 576px) {
  div.services-main div.services-name p {
    font-size: 35px;
    color: #ff7b54;
  }
  div.services-main div.disp-flex div.services-block {
    background: #ff7b54;
  }
  div.services-main div.disp-flex {
    margin: 2rem 0;
  }
  div.services-main div.disp-flex div.services-block {
    margin: 2rem;
  }
  div.services-main div.services-hidden-ui-ux p,
  div.services-hidden-vis-des p {
    padding: 0;
    margin: 1rem 0;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 450px) {
  div.services-main div.disp-flex {
    display: grid;
  }
}
/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.services-main div.disp-flex div.services-block {
    width: 200px;
  }
  div.services-main
    div.disp-flex
    div.services-block
    div.services-content
    div.services-btn
    div.view-more {
    font-size: 10px;
  }
  div.services-main div.services-hidden-ui-ux p,
  div.services-hidden-vis-des p {
    padding: 0;
    margin: 5px 0;
  }
  div.services-main
    div.disp-flex
    div.services-block
    div.services-content
    div.services-btn {
    font-size: 12px;
  }
  div.services-main
    div.disp-flex
    div.services-block
    div.services-content
    div.services-btn
    i {
    font-size: 12px;
  }
}
