div.skills-main {
  display: block;
  width: 100%;
  margin: 7rem 0;
}
div.skills-main p {
  text-align: center;
  font-size: 45px;
  color: #ff7b54;
  margin: 0;
}
div.skills-main p.technical-skills {
  font-size: 13px;
  color: #fff;
}
div.skills-main div.main-block {
  display: flex;
  margin: 2rem 15rem;
}

div.skills-main div.main-block div.skills-block div.skills-left {
  height: 300px;
  width: 400px;
  background: #ff7b54;
  padding: 1rem;
  cursor: pointer;
  box-shadow: 15px 15px #fff;
}
div.skills-main div.main-block div.skills-block div.skills-right {
  box-shadow: -15px 15px #fff;
}
div.skills-main div.main-block div.skills-block div.skills-left:hover {
  box-shadow: none;
  border-radius: 10px;
}
div.skills-main div.main-block div.skills-block div.skills-left p {
  font-size: 17px;
  margin-bottom: 1rem;
  color: #000;
}
div.skills-main div.main-block div.skills-block div.skills-left div.skills-sub {
  display: flex;
  height: 40px;
  align-items: center;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent {
  width: 230px;
  height: 10px;
  background: #000;
  border-radius: 20px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.tech-name {
  width: 120px;
  color: #000;
  font-size: 18px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.actual-percent {
  width: 200px;
  height: 10px;
  background: #fff;
  border-radius: 20px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-css {
  width: 180px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-js {
  width: 160px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-react {
  width: 130px;
}
div.skills-main div.main-block div.skills-block div.skills-left div.view-more {
  text-align: center;
  margin: 1rem 0;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.view-more
  a {
  font-size: 15px;
  background: rgb(21, 19, 19);
  color: #fff;
  padding: 8px 10px;
  border-radius: 10px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.view-more
  a:hover {
  background-color: #000;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.view-more
  a
  i {
  font-size: 12px;
  position: relative;
  top: 0px;
  transform: 0.4s;
  transition-duration: 500ms;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-python {
  width: 210px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-cpp {
  width: 180px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-java {
  width: 150px;
}
div.skills-main
  div.main-block
  div.skills-block
  div.skills-left
  div.skills-sub
  div.skills-percent
  div.tech-c {
  width: 180px;
}

/*** RESPONSIVE FOR LAPTOPS ***/

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 1400px) {
  div.skills-main div.main-block {
    display: flex;
    margin: 2rem 3rem;
  }
}
@media screen and (max-width: 972px) {
  div.skills-main {
    margin: 5rem 0;
  }
  div.skills-main div.main-block {
    display: flex;
    margin: 2rem 3rem;
  }
  div.skills-main div.main-block div.skills-block div.skills-left {
    height: 280px;
    width: 320px;
    padding: 0.5rem;
    box-shadow: 10px 10px #fff;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.tech-name {
    width: 90px;
    font-size: 14px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.view-more
    a {
    font-size: 12px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.view-more
    a
    i {
    font-size: 9px;
  }
}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.skills-main {
    margin: 0;
  }
  div.skills-main div.main-block {
    display: grid;
    margin: 0;

    justify-content: center;
  }
  div.skills-main div.main-block div.skills-block div.skills-left {
    margin: 2rem 0;
  }
  div.skills-main div.main-block div.skills-block div.skills-left {
    height: 300px;
    width: 380px;
    padding: 0.5rem;
    box-shadow: 10px 10px #fff;
  }
}

@media screen and (max-width: 576px) {
  div.skills-main p {
    font-size: 30px;
    color: #ff7b54;
  }
  div.skills-main div.main-block div.skills-block div.skills-left {
    background: #ff7b54;
  }
  div.skills-main {
    margin: 5rem 0;
  }
  div.skills-main div.main-block div.skills-block div.skills-left {
    height: 270px;
    width: 300px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.tech-name {
    width: 70px;
    font-size: 11px;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.skills-main div.main-block div.skills-block div.skills-left {
    height: 270px;
    width: 250px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent {
    width: 150px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-html {
    width: 130px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-css {
    width: 110px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-js {
    width: 100px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-react {
    width: 85px;
  }
  /**RIGHT */
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-python {
    width: 140px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-cpp {
    width: 120px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-java {
    width: 100px;
  }
  div.skills-main
    div.main-block
    div.skills-block
    div.skills-left
    div.skills-sub
    div.skills-percent
    div.tech-c {
    width: 120px;
  }
}
