div.qual-main {
  width: 100%;
  margin: 5rem 0;
}

div.qual-main div.qual-name {
  width: 100%;
  text-align: center;
}
div.qual-main div.qual-name p {
  font-size: 45px;
  margin: 0;
  color: #ff7b54;
}
div.qual-main div.qual-name h2 {
  font-size: 13px;
  color: #fff;
}

/***/
div.qual-main div.qual-container {
  margin: 0 3rem;
}
div.qual-main div.qual-container div.qual-education {
  margin: 1rem 0;
  text-align: center;
}

div.qual-main div.qual-container div.qual-sections {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
}
div.qual-main div.qual-container div.qual-sections div.content {
  width: 100%;
  margin: 3rem 0;
}

div.qual-data {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  column-gap: 1.5rem;
}
h3.qual-title {
  font-size: 30px;
}
.qual-subtitle {
  display: inline-block;
  font-size: 15px;
}
.qual-subtitle a {
  color: rgb(92, 90, 90);
}
div.qual-calender {
  font-size: 10px;
  margin: 0.25rem 0;
}

.align-right {
  margin: 0.25rem 0 0;
  padding: 0 0 1.5rem 0;
  display: grid;
  justify-content: end;
}
.align-left {
  text-align: left;
}
.qual-rounder {
  display: inline-block;
  width: 15px;
  height: 15px;
  background: #ff7b54;
  border-radius: 50%;
}
.qual-line {
  display: block;
  width: 1px;
  height: 100%;
  transform: translate(7px, -7px);
  background: #ff7b54;
}

/*** RESPONSIVE FOR LAPTOPS ***/

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
  h3.qual-title {
    font-size: 23px;
  }
  .qual-subtitle {
    font-size: 11px;
  }
  .qual-rounder {
    display: inline-block;
    width: 12px;
    height: 12px;
  }
  .qual-line {
    transform: translate(6px, -7px);
  }
}

/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.qual-main div.qual-container {
    margin: 0rem;
  }
  h3.qual-title {
    font-size: 18px;
  }
  .qual-subtitle {
    font-size: 10px;
  }
  div.qual-calender {
    font-size: 8px;
  }
  .qual-rounder {
    display: inline-block;
    width: 10px;
    height: 10px;
  }
  .qual-line {
    transform: translate(4.5px, -7px);
  }
  .align-right {
    margin: 0;
    padding: 0;
  }
}

@media screen and (max-width: 576px) {
  div.qual-main div.qual-name p {
    font-size: 45px;
    margin: 0;
    color: #ff7b54;
  }
  h3.qual-title {
    font-size: 12px;
  }
  .qual-subtitle {
    font-size: 8px;
  }
  div.qual-calender {
    font-size: 5px;
  }
  .qual-rounder {
    display: inline-block;
    width: 8px;
    height: 8px;
  }
  .qual-line {
    transform: translate(3.5px, -7px);
  }
  .align-right {
    margin: 0;
    padding: 0;
  }
  div.qual-main div.qual-name p {
    font-size: 25px;
  }
  div.qual-main div.qual-name h2 {
    font-size: 10px;
  }
  .align-right {
    margin: 0 0 0 0.75rem;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
