div.main_image img {
  height: 25rem;
  border-radius: 80px;
  animation: sagar_pic 5s ease-in-out infinite 1s;
  border: 7px solid rgb(80, 79, 79);
}
@keyframes sagar_pic {
  0% {
    border-radius: 60% 30% 50% 70% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 30% 50% 70% / 60% 30% 70% 40%;
  }
}

div.body {
  margin: 7rem 0;
  display: flex;
  height: 500px;
  align-items: center;
}

div.body div.marg {
  margin: 8rem;
}

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
  div.body div.marg {
    margin-left: 5.5rem;
  }
  div.main_image img {
    height: 20rem;
  }
}

/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.body div.marg {
    margin-left: 5rem;
  }
  div.body {
    margin: 0;
  }
}

@media screen and (max-width: 576px) {
  div.body {
    margin: 3rem 0 0 0;
    display: block;
  }
  div.main_image {
    margin: 0;
    text-align: center;
    width: 100%;
  }
  div.body div.marg {
    margin: 0;
  }
  div.main_image img {
    height: 18rem;
  }
}
/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.body {
    margin: 0;
    padding: 0;
    display: block;
  }
  div.main_image img {
    height: 14rem;
  }
  div.body {
    margin: 4.5rem 0;
  }
}
