.contact-main {
  display: block;
  width: 100%;
  margin: 5rem 0;
}

div.contact-main div.contact-name {
  width: 100%;
  text-align: center;
}
div.contact-main div.contact-name p {
  font-size: 45px;
  margin: 0;
  color: #ff7b54;
}

div.main-flex {
  width: 100%;
  display: flex;
  justify-content: center;
}
div.main-flex div.left {
  padding: 0.75rem;
  margin: 0 1rem;
  border-radius: 10px;
}
div.main-flex div.left div.left-block h2 {
  font-size: 25px;
  color: white;
  text-align: center;
}
div.main-flex div.left div.left-block div.contact-social {
  width: 250px;
  margin: 1rem 2rem;
  padding: 1rem 3rem;
  background: #ff7b54;
  display: grid;
  text-align: center;
  border-radius: 10px;
  color: #fff;
}
div.main-flex div.left div.left-block div.contact-social i.bx {
  margin: 0.25rem 0;
}

div.main-flex div.left div.left-block div.contact-social button {
  margin: 0.5rem;
  padding: 0.2rem 0.25rem;
  border-radius: 5px;
}
div.main-flex div.left div.left-block div.contact-social button a {
  font-size: 12px;
  color: #000;
}
/*****/

div.right {
  background: none;
  padding: 0.75rem;
  border-radius: 10px;
  align-content: center;
  display: grid;
}
div.main-flex div.right div.right-block h2 {
  font-size: 25px;
  color: white;
  text-align: center;
  margin: 0 0 2rem 0;
}

div.right div.right-block form div.form-div {
  position: relative;
  margin-bottom: 3rem;
  height: 4rem;
}
form .form-div .form-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #ff7b54;
  background: none;
  color: #ff7b54;
  border-radius: 1.5rem;
  padding: 1.5rem;
  z-index: 1;
  outline: none;
}

form .form-div label {
  z-index: 10;
  position: relative;
  top: -12px;
  left: 20px;
  padding: 10px 5px;
  background: rgb(10, 10, 10);
  border-radius: 50%;

  font-size: 13px;
}
form button.say-hello {
  padding: 0.75rem 1rem;
  border-radius: 12px;
  background: #ff7b54;
  border: 2px solid #ff7b54;
}
form button.say-hello:hover {
  background: rgb(10, 10, 10);
  color: #ff7b54;
}
form button.inactive {
  background: rgb(10, 10, 10);
  color: #fff;
  padding: auto 1rem;
  border: 2px solid #fff;
  display: none;
}
form button.active {
  display: none;
}

/*** RESPONSIVE FOR LAPTOPS ***/

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
  div.main-flex div.left div.left-block div.contact-social {
    width: 200px;
    margin: 1rem 1rem;
    padding: 1rem 3rem;
    background: #ff7b54;
    display: grid;
    text-align: center;
    border-radius: 10px;
    color: #fff;
  }
  div.main-flex div.left div.left-block h2 {
    font-size: 20px;
  }
  div.main-flex div.right div.right-block h2 {
    font-size: 20px;
  }
  form {
    width: 250px;
  }
}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
  div.main-flex div.left div.left-block div.contact-social {
    width: 180px;
    margin: 1rem 1rem;
    padding: 1rem 2rem;
    background: #ff7b54;
    display: grid;
    text-align: center;
    border-radius: 10px;
    color: #fff;
  }
  div.main-flex div.left div.left-block div.contact-social span {
    font-size: 12px;
  }
  div.main-flex div.left div.left-block h2 {
    font-size: 20px;
  }
  div.main-flex div.right div.right-block h2 {
    font-size: 20px;
  }
  form {
    width: 250px;
  }
  div.main-flex div.left div.left-block div.contact-social button {
    margin: 0.5rem;
    padding: 0.1rem 0rem;
    border-radius: 5px;
  }
  div.main-flex div.left div.left-block div.contact-social button a {
    font-size: 10px;
    color: #000;
  }
  form input {
    font-size: 11px;
  }
  form textarea {
    font-size: 11px;
  }
}

@media screen and (max-width: 576px) {
  div.contact-main div.contact-name p {
    font-size: 25px;
  }
  div.main-flex {
    display: block;
  }
  div.main-flex div.left,
  div.main-flex div.right {
    display: grid;
    justify-content: center;
  }
  div.main-flex div.right div.right-block h2 {
    font-size: 18px;
  }
  form .form-div label {
    font-size: 10px;
  }
  form .form-div .form-input {
    border: 1.5px solid #ff7b54;
    padding: 1.2rem;
  }

  form .form-div label {
    position: relative;
    top: -13px;
    left: 20px;
    padding: 3px;
    background: rgb(10, 10, 10);
    border-radius: 50%;
    z-index: 10;
    font-size: 13px;
  }
  form button {
    padding: 0.5rem 0.75rem;
    border-radius: 12px;
    background: #ff7b54;
    border: 1.5px solid #ff7b54;
  }
}

/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
  div.main-flex div.left div.left-block h2 {
    font-size: 15px;
  }
  div.contact-main div.contact-name p {
    font-size: 20px;
  }

  div.main-flex div.right div.right-block h2 {
    font-size: 15px;
  }
  form .form-div label {
    font-size: 10px;
  }
  form .form-div .form-input {
    border: 1px solid #ff7b54;
    padding: 1.2rem;
  }
  div.main-flex div.right {
    display: grid;
    justify-content: center;
  }
  form .form-div label {
    position: relative;
    top: -13px;
    left: 20px;
    padding: 3px;
    background: rgb(10, 10, 10);
    border-radius: 50%;
    z-index: 10;
    font-size: 13px;
  }
  form button {
    padding: 0.5rem 0.75rem;
    border-radius: 12px;
    background: #ff7b54;
    border: 1.5px solid #ff7b54;
    font-size: 12px;
  }
}
